import {request as Request} from "./Api";
import {types} from "./Types";
import $, {error} from 'jquery';
import M from "materialize-css";

const FILE_CODE = "clinic_img_1254";
export const get_dashboard_Admin_detail = () => {
    return async function (dispatch, getState) {
        await Request.get_admin_dashboard_data().then((res) => {
            dispatch({
                type: types.get_admin_dashboard_detail,
                payload: res.data.data,
            });
        });
    };
};


// upload kardan file becdn
// voroudi => input file
export const UploadedFile = (file) => {
    // console.log("files : " , file);
    $("#check_phone_num").fadeIn();
    //dispatch karadan request
    // estefade az async await baray asyncro kardan darkhast
    return async (dispatch, getState) => {
        localStorage.setItem(FILE_CODE, "");
        // darkhast zadan be object request.uploaded va etesal be api
        await Request.Uploaded(file).then(
            (response) => {
                // if response ok bood
                M.toast({html: "Your Document is uploaded"});
                $("#uploaded_img_clinic").fadeOut();
                // $(".loading_progress").fadeOut();
                localStorage.setItem(FILE_CODE, JSON.stringify(response));
                // if response eror bod
            },
            (error) => {
                dispatch({
                    type: "ERR_UPLOADED",
                    error: 401,
                });
            }
        );
    };
};


